import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false
import '@/utils/flexible'
import '../src/plugins/element.js'
import 'element-ui/lib/theme-chalk/index.css'
import './CSS/base.css'
import 'element-ui/lib/theme-chalk/base.css'
import axios from 'axios'
import VueAxios from 'vue-axios'
import '@/Scss/theme.scss'
Vue.use(VueAxios, axios)
import VueWatermark from 'vue-watermark'
Vue.component('VueWatermark', VueWatermark)
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
Vue.use(Viewer)
Viewer.setDefaults({
  Options: {
    inline: true, // inline
    button: true, // 
    navbar: true, // 
    title: true, // alt
    toolbar: true, // 
    tooltip: true, // true
    fullscreen: true, // true
    loading: true, // loadingtrue
    loop: true, // true
    movable: true, // true
    zoomable: true, // true
    rotatable: true, // true
    scalable: true, // true
    toggleOnDblclick: true, // true
    transition: true, //  CSS3 true
    keyboard: true, // true
    zoomRatio: 0.4, // 0.1
    minZoomRatio: 0.01, // 0.01
    maxZoomRatio: 100, // 100
    url: 'data-source' //  url
  }
})

// 
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
NProgress.configure({
  easing: 'ease', // 
  speed: 500, // 
  showSpinner: false, // ico
  trickleSpeed: 200, // 
  minimum: 0.3 // 
})
//
router.beforeEach((to, from, next) => {
  // 
  NProgress.start()
  // 
  NProgress.inc() //100%
  next()
})
//
router.afterEach(() => {
  // 
  NProgress.done()
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
